/* RESET STYLES */

@import-normalize;

* {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/*-------------- ALL PAGES --------------*/
main {
  /* to keep footer at bottom */
  min-height: 58vh;
  margin: 0;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style-type: none;
}

/*-------------- #HEADER --------------*/

.logo {
  position: absolute;
  top: 5px;
  left: 5px;
}

.navbar {
  background-color: #256d85;
  color: white;
  display: flex;
  justify-content: space-between;
}

.nav-main {
  display: flex;
  gap: 50px;
  padding: 20px 0;
  margin-left: 230px;
}

.nav-main-item {
  display: flex;
  flex-direction: column;
  font-size: 13px;
  text-transform: uppercase;
  max-width: 51px;
}

.nav-main-item:hover {
  font-weight: bold;
}

.nav-main-icon {
  max-width: 51px;
  max-height: 51px;
}

.nav-main-text {
  align-self: center;
}

.nav-sub {
  display: flex;
  gap: 20px;
}

.login {
  background-color: #4c6793;
  color: white;
  border: 3px solid white;
  width: 270px;
  height: 70px;
  margin-top: 15px;
  margin-right: 5px;
  font-size: 13px;
  display: flex;
  align-items: center;
}

.login input {
  border: 1px solid white;
  max-width: 90px;
}

.login-group {
  display: flex;
  gap: 5px;
}

.login-set {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  gap: 3px;
}

.login-button {
  align-self: flex-end;
  background-color: white;
  color: #256d85;
  max-height: 17.5px;
  margin-left: 5px;
  padding: 3px;
  display: flex;
  align-items: center;
  border: 1px solid #256d85;
}

.login-button:hover {
  background-color: #256d85;
  color: white;
  font-weight: bold;
  border: 1px solid white;
  cursor: pointer;
}

.signup {
  margin-top: 5px;
  margin-left: 5px;
}

.signup-link {
  text-decoration: underline;
}

.signup-link:hover {
  font-weight: bold;
}

.nav-sub {
  background-color: #4c6793;
}

.nav-sub-list {
  display: flex;
  gap: 40px;
  padding: 10px 0;
  margin-left: 230px;
  color: white;
}

.nav-sub-link {
  width: 42px;
  font-size: 14px;
}

.nav-sub-link:hover {
  font-weight: bold;
}

/*-------------- #FOOTER --------------*/

footer {
  background-color: #e3e9f0;
  margin-top: 60px;
  padding: 10px 0;
  color: grey;
}

.footer-links {
  display: flex;
  justify-content: space-around;
}

.footer-link {
  width: 140px;
}

.footer-link:hover {
  color: black;
  font-weight: bold;
}

/*-------------- #HOME --------------*/

.center-page {
  margin-left: 20%;
  margin-right: 20%;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  text-align: justify;
}

.home-intro-p {
  margin-bottom: 40px;
}

.read-more {
  color: #5c2e7e;
}

.read-more:hover {
  font-weight: bold;
}

.home-main-image {
  border: 3px solid #5c2e7e;
  max-width: 800px;
  align-self: center;
}

/*-------------- #CONTACT --------------*/

.contact-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/*-------------- #TASKS --------------*/

.tasks-page {
  display: flex;
  gap: 50px;
}

.tasks-header {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.tasks-header-texts {
  display: flex;
  flex-direction: column;
}

.tasks-header h1 {
  font-size: 20px;
  align-self: flex-start;
  margin-bottom: 5px;
}

.tasks-avatar {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
}

.tasks-section {
  display: flex;
  justify-content: space-between;
}

.tasks-section-spread {
  align-self: center;
}

.tasks-subsection {
  width: 400px;
  /* border: 1px solid black; */
}

.tasks-section h3 {
  margin-bottom: 20px;
}

.total-tasks {
  margin-bottom: 20px;
}

.task-li {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.task-item-name {
  display: flex;
  margin-bottom: 5px;
  gap: 4px;
}

.group-color {
  min-width: 20px;
  min-height: 20px;
  max-width: 20px;
  max-height: 20px;
  background-color: rgb(54, 54, 142);
}

.tasks-current {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.task-options {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.task-options > * {
  color: seagreen;
}

.task-status {
  color: rgb(172, 23, 53);
  margin-left: 25px;
}

.friend-li {
  display: flex;
  gap: 5px;
}

.friend-avatar {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

.friend-task {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.task-name {
  font-weight: bold;
}

.tasks-page h3 {
  font-weight: normal;
}

.comment-li {
  display: flex;
  flex-direction: column;
}

.friend-comment {
  display: flex;
  margin-left: 23px;
  gap: 5px;
  margin-bottom: 15px;
}

.friend-comment-pair {
  display: flex;
  flex-direction: column;
}

.friend-says {
  font-weight: bold;
}

.friend-comment-text {
  font-size: 13px;
}

.upcoming-events {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.upcoming-events h3 {
  margin-bottom: 10px;
}

.event-table {
  border-collapse: collapse;
  border: 2px solid rgb(200, 200, 200);
}

.event-table td,
th {
  border: 1px solid rgb(190, 190, 190);
  padding: 10px;
}

.upcoming-events button {
  width: 120px;
  align-self: center;
  background-color: aliceblue;
  border: 1px solid grey;
  font-weight: bold;
  padding: 3px 0;
  border-radius: 5px;
}

.upcoming-events button:hover {
  background-color: #256d85;
  color: white;
  border: 1px solid white;
  cursor: pointer;
}

.event-link {
  font-weight: bold;
  color: rgb(54, 54, 142);
}

.tasks-completed h3 {
  margin-bottom: 20px;
}

.completed-tasks-list {
  /* justify-self: left;
  display: flex;
  flex-direction: column;
  max-height: 500px;
  flex-wrap: wrap;
  gap: 10px; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.completed-tasks-list li {
  margin-right: 30px;
}
